export async function getLatestContent(limit: number = 10): Promise<Content[]> {
  // TODO: Replace with real database query
  return mockContent.slice(0, limit)
}

export async function getContentBySlug(slug: string, type: string): Promise<Content | null> {
  // TODO: Replace with real database query
  return mockContent.find((c) => c.slug === slug && c.type === type) || null
}

export async function getContentByCategory(category: string): Promise<Content[]> {
  // TODO: Replace with real database query
  return mockContent.filter((c) => c.category === category)
}

export async function searchContent(query: string): Promise<Content[]> {
  // TODO: Replace with real database query
  const lowerQuery = query.toLowerCase()
  return mockContent.filter(
    (c) =>
      c.title.toLowerCase().includes(lowerQuery) ||
      c.excerpt.toLowerCase().includes(lowerQuery) ||
      c.tags.some((tag) => tag.toLowerCase().includes(lowerQuery))
  )
}

export async function getAllCategories(): Promise<Category[]> {
  // TODO: Replace with real database query
  const categories = ['Design', 'Editorial', 'Technology', 'Culture']
  return categories.map((name, index) => ({
    id: String(index),
    name,
    slug: name.toLowerCase(),
    createdAt: new Date(),
  }))
}
